<template><section>
<h2>Timeline 时间线</h2>
<p>介绍Timeline 时间线的使用</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n&lt;div class=\&quot;block\&quot;&gt;\n  &lt;div class=\&quot;radio\&quot;&gt;\n    排序：\n    &lt;dt-radio-group v-model=\&quot;reverse\&quot;&gt;\n      &lt;dt-radio :label=\&quot;true\&quot;&gt;倒序&lt;/dt-radio&gt;\n      &lt;dt-radio :label=\&quot;false\&quot;&gt;正序&lt;/dt-radio&gt;\n    &lt;/dt-radio-group&gt;\n  &lt;/div&gt;\n\n  &lt;dt-timeline :reverse=\&quot;reverse\&quot;&gt;\n    &lt;dt-timeline-item v-for=\&quot;(activity, index) in activities\&quot; :key=\&quot;index\&quot; :timestamp=\&quot;activity.timestamp\&quot;&gt;\n      {{activity.content}}\n    &lt;/dt-timeline-item&gt;\n  &lt;/dt-timeline&gt;\n&lt;/div&gt;\n\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data() {\n      return {\n        reverse: true,\n        activities: [{\n          content: '活动按期开始',\n          timestamp: '2018-04-15'\n        }, {\n          content: '通过审核',\n          timestamp: '2018-04-13'\n        }, {\n          content: '创建成功',\n          timestamp: '2018-04-11'\n        }]\n      };\n    }\n  };\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
<div class="block">
  <div class="radio">
    排序：
    <dt-radio-group v-model="reverse">
      <dt-radio :label="true">倒序</dt-radio>
      <dt-radio :label="false">正序</dt-radio>
    </dt-radio-group>
  </div>

  <dt-timeline :reverse="reverse">
    <dt-timeline-item v-for="(activity, index) in activities" :key="index" :timestamp="activity.timestamp">
      {{activity.content}}
    </dt-timeline-item>
  </dt-timeline>
</div>

</template>


</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"block"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"radio"</span>&gt;</span>
    排序：
    <span class="hljs-tag">&lt;<span class="hljs-name">dt-radio-group</span> <span class="hljs-attr">v-model</span>=<span class="hljs-string">"reverse"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">dt-radio</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"true"</span>&gt;</span>倒序<span class="hljs-tag">&lt;/<span class="hljs-name">dt-radio</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">dt-radio</span> <span class="hljs-attr">:label</span>=<span class="hljs-string">"false"</span>&gt;</span>正序<span class="hljs-tag">&lt;/<span class="hljs-name">dt-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">dt-radio-group</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

  <span class="hljs-tag">&lt;<span class="hljs-name">dt-timeline</span> <span class="hljs-attr">:reverse</span>=<span class="hljs-string">"reverse"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dt-timeline-item</span>
      <span class="hljs-attr">v-for</span>=<span class="hljs-string">"(activity, index) in activities"</span>
      <span class="hljs-attr">:key</span>=<span class="hljs-string">"index"</span>
      <span class="hljs-attr">:timestamp</span>=<span class="hljs-string">"activity.timestamp"</span>&gt;</span>
      {{activity.content}}
    <span class="hljs-tag">&lt;/<span class="hljs-name">dt-timeline-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">dt-timeline</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>

<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data() {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">reverse</span>: <span class="hljs-literal">true</span>,
        <span class="hljs-attr">activities</span>: [{
          <span class="hljs-attr">content</span>: <span class="hljs-string">'活动按期开始'</span>,
          <span class="hljs-attr">timestamp</span>: <span class="hljs-string">'2018-04-15'</span>
        }, {
          <span class="hljs-attr">content</span>: <span class="hljs-string">'通过审核'</span>,
          <span class="hljs-attr">timestamp</span>: <span class="hljs-string">'2018-04-13'</span>
        }, {
          <span class="hljs-attr">content</span>: <span class="hljs-string">'创建成功'</span>,
          <span class="hljs-attr">timestamp</span>: <span class="hljs-string">'2018-04-11'</span>
