<template><section>
<h2>Steps 步骤条</h2>
<p>介绍Steps 步骤条的使用</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;div&gt;\n    &lt;dt-steps :active=\&quot;active\&quot; finish-status=\&quot;success\&quot;&gt;\n      &lt;dt-step title=\&quot;步骤 1\&quot;&gt;&lt;/dt-step&gt;\n      &lt;dt-step title=\&quot;步骤 2\&quot;&gt;&lt;/dt-step&gt;\n      &lt;dt-step title=\&quot;步骤 3\&quot;&gt;&lt;/dt-step&gt;\n    &lt;/dt-steps&gt;\n\n    &lt;dt-button style=\&quot;margin-top: 12px;\&quot; @click=\&quot;next\&quot;&gt;下一步&lt;/dt-button&gt;\n\n  &lt;/div&gt;\n&lt;/template&gt;\n\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data() {\n      return {\n        active: 0\n      };\n    },\n\n    methods: {\n      next() {\n        if (this.active++ &gt; 2) this.active = 0;\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <div>
    <dt-steps :active="active" finish-status="success">
      <dt-step title="步骤 1"></dt-step>
      <dt-step title="步骤 2"></dt-step>
      <dt-step title="步骤 3"></dt-step>
    </dt-steps>

    <dt-button style="margin-top: 12px;" @click="next">下一步</dt-button>

  </div>
</template>



</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">dt-steps</span> <span class="hljs-attr">:active</span>=<span class="hljs-string">"active"</span> <span class="hljs-attr">finish-status</span>=<span class="hljs-string">"success"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">dt-step</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"步骤 1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">dt-step</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">dt-step</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"步骤 2"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">dt-step</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">dt-step</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"步骤 3"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">dt-step</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">dt-steps</span>&gt;</span>

    <span class="hljs-tag">&lt;<span class="hljs-name">dt-button</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-top: 12px;"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"next"</span>&gt;</span>下一步<span class="hljs-tag">&lt;/<span class="hljs-name">dt-button</span>&gt;</span>

  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
