                            <div class="source" slot="source"><template>
  <dt-popover placement="top-start" title="标题" width="200" trigger="hover" content="这是一段内容,这是一段内容,这是一段内容,这是一段内容。">
    <dt-button slot="reference">hover 激活</dt-button>
  </dt-popover>

  <dt-popover placement="bottom" title="标题" width="200" trigger="click" content="这是一段内容,这是一段内容,这是一段内容,这是一段内容。">
    <dt-button slot="reference">click 激活</dt-button>
  </dt-popover>

  <dt-popover ref="popover" placement="right" title="标题" width="200" trigger="focus" content="这是一段内容,这是一段内容,这是一段内容,这是一段内容。">
  </dt-popover>
  <dt-button v-popover:popover="">focus 激活</dt-button>

  <dt-popover placement="bottom" title="标题" width="200" trigger="manual" content="这是一段内容,这是一段内容,这是一段内容,这是一段内容。" v-model="visible">
    <dt-button slot="reference" @click="visible = !visible">手动激活</dt-button>
  </dt-popover>
</template>


