<template><section>

<h2>InfiniteScroll 无限滚动</h2>
<p>介绍CInfiniteScroll 无限滚动的使用</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;ul class=\&quot;infinite-list\&quot; v-infinite-scroll=\&quot;load\&quot; style=\&quot;overflow:auto\&quot;&gt;\n    &lt;li v-for=\&quot;i in count\&quot; class=\&quot;infinite-list-item\&quot;&gt;{{ i }}&lt;/li&gt;\n  &lt;/ul&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data () {\n      return {\n        count: 0\n      }\n    },\n    methods: {\n      load () {\n        this.count += 2\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <ul class="infinite-list" v-infinite-scroll="load" style="overflow:auto">
    <li v-for="i in count" class="infinite-list-item">{{ i }}</li>
  </ul>
</template>


</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">ul</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"infinite-list"</span> <span class="hljs-attr">v-infinite-scroll</span>=<span class="hljs-string">"load"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"overflow:auto"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">li</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"i in count"</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"infinite-list-item"</span>&gt;</span>{{ i }}<span class="hljs-tag">&lt;/<span class="hljs-name">li</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">ul</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>

<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">count</span>: <span class="hljs-number">0</span>
      }
    },
    <span class="hljs-attr">methods</span>: {
      load () {
        <span class="hljs-keyword">this</span>.count += <span class="hljs-number">2</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>
<style>
.infinite-list {
    height: 300px;
    padding: 0;
    margin: 0;
    list-style: none;
}
.infinite-list .infinite-list-item {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  background: #e8f3fe;
  margin: 10px;
  color: #7dbcfc;
}
</style>
<script>
  export default {
    data () {
      return {
        count: 0
      }
    },
    methods: {
      load () {
        this.count += 2
      }
    }
  }
</script>


