<template><section>

<h2>色卡</h2>
<p>介绍ColorPicker 颜色选择器的使用
<template></template></p>
  <div class="color-card">
    <div v-for="item in data" style="padding:10px;width: 800px;">
      <span style="font-size:30px;">### {{item.name+"（"+item.code+")"}}</span>
      <div v-if="item.backgroundColor" v-for="bItem in item.backgroundColor" :style="{border: '1px solid #ccc', padding: '20px',width:'700px',height:'200px',backgroundColor:bItem.color}">
        <span v-for="cItem in item.color" :style="{padding: '10px',color: cItem.color}">{{cItem.label+"-"+cItem.color}}</span>
        <ul>
          <li v-for="it in item.other">
            <div :style="{backgroundColor: it.color}"></div>
            {{it.label+"-"+it.color}}
          </li>
        </ul>
      </div>
      <ul>
        <li v-for="it in item.backgroundColor">
          <div :style="{backgroundColor: it.color}"></div>
          {{it.label+"-"+it.color}}
        </li>
        <li v-for="it in item.color">
          <div :style="{backgroundColor: it.color}"></div>
          {{it.label+"-"+it.color}}
        </li>
        <li v-for="it in item.other">
          <div :style="{backgroundColor: it.color}"></div>
          {{it.label+"-"+it.color}}
        </li>
      </ul>
    </div>
    <div style="clear:both;">
  </div>
</div></section></template>
<style>
  .color-card{
    padding: 10px;
    /* display: flex;
    flex-wrap:wrap */
  }
  .color-card>div{
    /* clear:both; */
  }
  .color-card ul{
    padding: 10px;
    list-style:none;
    display: flex;
    flex-wrap:wrap
  }
  .color-card ul>li{
    width:120px;
    height:80px;
    margin-left: 4px;
    list-style:none; /* 将默认的列表符号去掉 */
    float: left; /* 往左浮动 */
    display: block;
  }
  .color-card ul>li>div{
    width:100px;
    height:40px;
    border: 1px solid #ccc;
  }
</style>
<script>
  export default {
    data() {
      return {
        data: [{
          code: 'dark',
          name: '暗黑',
          color: [{
            label: '文字1',
            color: '#9cdcfe'
          }, {
            label: '文字2',
            color: '#fff'
          }, {
            label: '文字3',
            color: '#b5816b'
          }, {
            label: '文字4',
            color: '#b5cea8'
          }],
          backgroundColor: [{
            label: '背景',
            color: '#1e1e1e'
          }],
          other: [{
            label: '图标1',
            color: '#007acc'
          },{
            label: '图标2',
            color: '#519aba'
          },{
            label: '背景1',
            color: '#858585'
          },{
            label: '背景2',
            color: '#333333'
          }]
        },{
          code: 'sgcc',
          name: '国网绿',
          color: [{
            label: '文字',
            color: '#ccc'
          }, {
            label: '文字',
            color: '#fff'
          }],
          backgroundColor: [{
            label: '背景',
            color: '#006865'
          }],
          other: [{
            label: '边框',
            color: '#aa7602'
          },{
            label: '边框',
            color: '#377456'
          },{
            label: '边框',
            color: '#23476a'
          }]
        },{
          code: 'google',
          name: '谷歌',
          color: [{
            label: 'G',
            color: '#4285f4'
          }, {
            label: 'O',
            color: 'rgb(234 67 53)'
          }, {
            label: 'O',
            color: 'rgb(251 188 5)'
          }, {
            label: 'L',
            color: 'rgb(52 168 83)'
          }],
          backgroundColor: [{
            label: '背景',
            color: '#fff'
          }],
          other: [{
            label: '边框',
            color: '#fff'
          }]
        }]
      }
    }
  };
</script>


