<template><section>
<h2>Drawer 抽屉</h2>
<p>介绍Drawer 抽屉的使用</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;div&gt;\n    &lt;dt-radio-group v-model=\&quot;direction\&quot;&gt;\n      &lt;dt-radio label=\&quot;ltr\&quot;&gt;从左往右开&lt;/dt-radio&gt;\n      &lt;dt-radio label=\&quot;rtl\&quot;&gt;从右往左开&lt;/dt-radio&gt;\n      &lt;dt-radio label=\&quot;ttb\&quot;&gt;从上往下开&lt;/dt-radio&gt;\n      &lt;dt-radio label=\&quot;btt\&quot;&gt;从下往上开&lt;/dt-radio&gt;\n    &lt;/dt-radio-group&gt;\n\n    &lt;dt-button @click=\&quot;drawer = true\&quot; type=\&quot;primary\&quot; style=\&quot;margin-left: 16px;\&quot;&gt;\n      点我打开\n    &lt;/dt-button&gt;\n\n    &lt;dt-drawer title=\&quot;我是标题\&quot; :visible.sync=\&quot;drawer\&quot; :direction=\&quot;direction\&quot; :before-close=\&quot;handleClose\&quot;&gt;\n      &lt;span&gt;我来啦!&lt;/span&gt;\n    &lt;/dt-drawer&gt;\n\n  &lt;/div&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data() {\n      return {\n        drawer: false,\n        direction: 'rtl',\n      };\n    },\n    methods: {\n      handleClose(done) {\n        this.$confirm('确认关闭？')\n          .then(_ =&gt; {\n            done();\n          })\n          .catch(_ =&gt; {});\n      }\n    }\n  };\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <div>
    <dt-radio-group v-model="direction">
      <dt-radio label="ltr">从左往右开</dt-radio>
      <dt-radio label="rtl">从右往左开</dt-radio>
      <dt-radio label="ttb">从上往下开</dt-radio>
      <dt-radio label="btt">从下往上开</dt-radio>
    </dt-radio-group>

    <dt-button @click="drawer = true" type="primary" style="margin-left: 16px;">
      点我打开
    </dt-button>

    <dt-drawer title="我是标题" :visible.sync="drawer" :direction="direction" :before-close="handleClose">
      <span>我来啦!</span>
    </dt-drawer>

  </div>
</template>


</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
