      <span class="hljs-tag">&lt;<span class="hljs-name">dt-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"ltr"</span>&gt;</span>从左往右开<span class="hljs-tag">&lt;/<span class="hljs-name">dt-radio</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">dt-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"rtl"</span>&gt;</span>从右往左开<span class="hljs-tag">&lt;/<span class="hljs-name">dt-radio</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">dt-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"ttb"</span>&gt;</span>从上往下开<span class="hljs-tag">&lt;/<span class="hljs-name">dt-radio</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">dt-radio</span> <span class="hljs-attr">label</span>=<span class="hljs-string">"btt"</span>&gt;</span>从下往上开<span class="hljs-tag">&lt;/<span class="hljs-name">dt-radio</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">dt-radio-group</span>&gt;</span>

    <span class="hljs-tag">&lt;<span class="hljs-name">dt-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"drawer = true"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">style</span>=<span class="hljs-string">"margin-left: 16px;"</span>&gt;</span>
      点我打开
    <span class="hljs-tag">&lt;/<span class="hljs-name">dt-button</span>&gt;</span>

    <span class="hljs-tag">&lt;<span class="hljs-name">dt-drawer</span>
      <span class="hljs-attr">title</span>=<span class="hljs-string">"我是标题"</span>
