      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">drawer</span>: <span class="hljs-literal">false</span>,
        <span class="hljs-attr">direction</span>: <span class="hljs-string">'rtl'</span>,
      };
    },
    <span class="hljs-attr">methods</span>: {
      handleClose(done) {
        <span class="hljs-keyword">this</span>.$confirm(<span class="hljs-string">'确认关闭？'</span>)
          .then(<span class="hljs-function"><span class="hljs-params">_</span> =&gt;</span> {
            done();
          })
          .catch(<span class="hljs-function"><span class="hljs-params">_</span> =&gt;</span> {});
      }
    }
  };
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
  export default {
    data() {
      return {
        drawer: false,
        direction: 'rtl',
      };
    },
    methods: {
      handleClose(done) {
        this.$confirm('确认关闭？')
          .then(_ => {
            done();
          })
          .catch(_ => {});
      }
    }
  };
</script>


