<template><section>

<h2>Form 表单</h2>
<p>介绍Form 表单的使用</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n&lt;dt-form ref=\&quot;form\&quot; :model=\&quot;form\&quot; label-width=\&quot;80px\&quot; style=\&quot;width:460px\&quot;&gt;\n  &lt;dt-form-item label=\&quot;活动名称\&quot;&gt;\n    &lt;dt-input v-model=\&quot;form.name\&quot;&gt;&lt;/dt-input&gt;\n  &lt;/dt-form-item&gt;\n  &lt;dt-form-item label=\&quot;活动区域\&quot;&gt;\n    &lt;dt-select v-model=\&quot;form.region\&quot; placeholder=\&quot;请选择活动区域\&quot;&gt;\n      &lt;dt-option label=\&quot;区域一\&quot; value=\&quot;shanghai\&quot;&gt;&lt;/dt-option&gt;\n      &lt;dt-option label=\&quot;区域二\&quot; value=\&quot;beijing\&quot;&gt;&lt;/dt-option&gt;\n    &lt;/dt-select&gt;\n  &lt;/dt-form-item&gt;\n  &lt;dt-form-item label=\&quot;活动时间\&quot;&gt;\n    &lt;dt-col :span=\&quot;11\&quot;&gt;\n      &lt;dt-date-picker type=\&quot;date\&quot; placeholder=\&quot;选择日期\&quot; v-model=\&quot;form.date1\&quot; style=\&quot;width: 100%;\&quot;&gt;&lt;/dt-date-picker&gt;\n    &lt;/dt-col&gt;\n    &lt;dt-col class=\&quot;line\&quot; :span=\&quot;2\&quot;&gt;-&lt;/dt-col&gt;\n    &lt;dt-col :span=\&quot;11\&quot;&gt;\n      &lt;dt-time-picker placeholder=\&quot;选择时间\&quot; v-model=\&quot;form.date2\&quot; style=\&quot;width: 100%;\&quot;&gt;&lt;/dt-time-picker&gt;\n    &lt;/dt-col&gt;\n  &lt;/dt-form-item&gt;\n  &lt;dt-form-item label=\&quot;即时配送\&quot;&gt;\n    &lt;dt-switch v-model=\&quot;form.delivery\&quot;&gt;&lt;/dt-switch&gt;\n  &lt;/dt-form-item&gt;\n  &lt;dt-form-item label=\&quot;活动性质\&quot;&gt;\n    &lt;dt-checkbox-group v-model=\&quot;form.type\&quot;&gt;\n      &lt;dt-checkbox label=\&quot;美食/餐厅线上活动\&quot; name=\&quot;type\&quot;&gt;&lt;/dt-checkbox&gt;\n      &lt;dt-checkbox label=\&quot;地推活动\&quot; name=\&quot;type\&quot;&gt;&lt;/dt-checkbox&gt;\n      &lt;dt-checkbox label=\&quot;线下主题活动\&quot; name=\&quot;type\&quot;&gt;&lt;/dt-checkbox&gt;\n      &lt;dt-checkbox label=\&quot;单纯品牌曝光\&quot; name=\&quot;type\&quot;&gt;&lt;/dt-checkbox&gt;\n    &lt;/dt-checkbox-group&gt;\n  &lt;/dt-form-item&gt;\n  &lt;dt-form-item label=\&quot;特殊资源\&quot;&gt;\n    &lt;dt-radio-group v-model=\&quot;form.resource\&quot;&gt;\n      &lt;dt-radio label=\&quot;线上品牌商赞助\&quot;&gt;&lt;/dt-radio&gt;\n      &lt;dt-radio label=\&quot;线下场地免费\&quot;&gt;&lt;/dt-radio&gt;\n    &lt;/dt-radio-group&gt;\n  &lt;/dt-form-item&gt;\n  &lt;dt-form-item label=\&quot;活动形式\&quot;&gt;\n    &lt;dt-input type=\&quot;textarea\&quot; v-model=\&quot;form.desc\&quot;&gt;&lt;/dt-input&gt;\n  &lt;/dt-form-item&gt;\n  &lt;dt-form-item&gt;\n    &lt;dt-button type=\&quot;primary\&quot; @click=\&quot;onSubmit\&quot;&gt;立即创建&lt;/dt-button&gt;\n    &lt;dt-button&gt;取消&lt;/dt-button&gt;\n  &lt;/dt-form-item&gt;\n&lt;/dt-form&gt;\n&lt;/template&gt;\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    data() {\n      return {\n        form: {\n          name: '',\n          region: '',\n          date1: '',\n          date2: '',\n          delivery: false,\n          type: [],\n          resource: '',\n          desc: ''\n        }\n      }\n    },\n    methods: {\n      onSubmit() {\n        console.log('submit!');\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
<dt-form ref="form" :model="form" label-width="80px" style="width:460px">
  <dt-form-item label="活动名称">
    <dt-input v-model="form.name"></dt-input>
  </dt-form-item>
  <dt-form-item label="活动区域">
    <dt-select v-model="form.region" placeholder="请选择活动区域">
      <dt-option label="区域一" value="shanghai"></dt-option>
      <dt-option label="区域二" value="beijing"></dt-option>
    </dt-select>
  </dt-form-item>
  <dt-form-item label="活动时间">
    <dt-col :span="11">
      <dt-date-picker type="date" placeholder="选择日期" v-model="form.date1" style="width: 100%;"></dt-date-picker>
    </dt-col>
    <dt-col class="line" :span="2">-</dt-col>
    <dt-col :span="11">
      <dt-time-picker placeholder="选择时间" v-model="form.date2" style="width: 100%;"></dt-time-picker>
    </dt-col>
  </dt-form-item>
  <dt-form-item label="即时配送">
    <dt-switch v-model="form.delivery"></dt-switch>
  </dt-form-item>
  <dt-form-item label="活动性质">
    <dt-checkbox-group v-model="form.type">
      <dt-checkbox label="美食/餐厅线上活动" name="type"></dt-checkbox>
      <dt-checkbox label="地推活动" name="type"></dt-checkbox>
      <dt-checkbox label="线下主题活动" name="type"></dt-checkbox>
      <dt-checkbox label="单纯品牌曝光" name="type"></dt-checkbox>
    </dt-checkbox-group>
  </dt-form-item>
  <dt-form-item label="特殊资源">
    <dt-radio-group v-model="form.resource">
      <dt-radio label="线上品牌商赞助"></dt-radio>
      <dt-radio label="线下场地免费"></dt-radio>
    </dt-radio-group>
  </dt-form-item>
  <dt-form-item label="活动形式">
    <dt-input type="textarea" v-model="form.desc"></dt-input>
  </dt-form-item>
  <dt-form-item>
    <dt-button type="primary" @click="onSubmit">立即创建</dt-button>
    <dt-button>取消</dt-button>
  </dt-form-item>
</dt-form>
</template>

</div>
                            
