<template><section>
<h2>Notification 通知</h2>
<p>介绍Notification 通知的使用</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n  &lt;dt-button plain @click=\&quot;open1\&quot;&gt;\n    可自动关闭\n  &lt;/dt-button&gt;\n  &lt;dt-button plain @click=\&quot;open2\&quot;&gt;\n    不会自动关闭\n    &lt;/dt-button&gt;\n&lt;/template&gt;\n\n\n\n&quot;,&quot;script&quot;:&quot;\n  export default {\n    methods: {\n      open1() {\n        const h = this.$createElement;\n\n        this.$notify({\n          title: '标题名称',\n          message: h('i', { style: 'color: teal'}, '这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案')\n        });\n      },\n\n      open2() {\n        this.$notify({\n          title: '提示',\n          message: '这是一条不会自动关闭的消息',\n          duration: 0\n        });\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><template>
  <dt-button plain="" @click="open1">
    可自动关闭
  </dt-button>
  <dt-button plain="" @click="open2">
    不会自动关闭
    </dt-button>
</template>



</div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
