  <span class="hljs-tag">&lt;/<span class="hljs-name">dt-button</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">dt-button</span>
    <span class="hljs-attr">plain</span>
    <span class="hljs-attr">@click</span>=<span class="hljs-string">"open2"</span>&gt;</span>
